@charset "UTF-8";
main {
  background-image: url("/images/bg-image.jpg");
  width: auto;
  min-height: 760px;
  background-repeat: repeat;
  background-size: contain;
}

.col-white {
  background: white;
}

.img-sm {
  width: 200px;
  height: auto;
  object-fit: contain;
  background: #F3F3F3;
}

.img-lg {
  width: 100%;
  height: auto;
  object-fit: contain;
  background: #F3F3F3;
}

.bg-plan {
  background-color: #F3F3F3;
}

/* タブの色変更 */
.nav-pills .nav-link.active {
  background-color: #DAA4AA;
  color: black;
}

/* タブの文字色変更 */
.nav-pills .nav-link {
  color: black;
  font-weight: bold;
}

/*　仮image　*/
.img-sm {
  width: 200px;
  height: 150px;
  object-fit: contain;
  background: #F3F3F3;
}

.img-lg {
  width: 100%;
  height: auto;
  object-fit: contain;
  background: #F3F3F3;
}

.background-container {
  position: relative;
  justify-content: center;
}

.add-place img:hover {
  cursor: pointer;
}
.add-place p:hover {
  cursor: pointer;
}

#reload-places:hover {
  cursor: pointer;
}